.simple-picture {
	display: block;

	background-color: var(--Content-100);
	background-image: url(../images/simple-picture/img.jpg);
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;

	img {
		width: 100%;
		height: 100%;

		object-fit: cover;
		object-position: center;
	}
}
